.tech {
  display: -webkit-box;
  display: flex;
}

.tech--right {
  -webkit-box-pack: end;
          justify-content: flex-end;
}

.tech__icon {
  position: relative;
  padding: 14px;
  img {
    width: 40px;
    height: 40px;
  }
}

.tech__icon:hover .tooltip {
  visibility: visible;
}
